const getShortDayName = (day: number): string => {
  const days = ['Вс', 'Пон', 'Вт', 'Ср', 'Чт', 'Пт', 'Сб']
  return days[day]
}

const getFullDayName = (day: number): string => {
  const days = ['Воскресенье', 'Понедельник', 'Вторник', 'Среда', 'Четверг', 'Пятница', 'Суббота']
  return days[day]
}

const getMonthName = (month: number): string => {
  const months = ['января', 'февраля', 'марта', 'апреля', 'мая', 'июня', 'июля', 'августа', 'сентября', 'октября', 'ноября', 'декабря']
  return months[month]
}

const getGreetings = (): string => {
  const currentTime = new Date().getHours()
  if (currentTime >= 0 && currentTime < 4) return 'Доброй ночи'
  if (currentTime >= 4 && currentTime < 12) return 'Доброе утро'
  if (currentTime >= 12 && currentTime < 17) return 'Добрый день'
  if (currentTime >= 17 && currentTime <= 23) return 'Добрый вечер'
  return 'Добрый день'
}

export {
  getShortDayName,
  getFullDayName,
  getMonthName,
  getGreetings,
}