const setStorageValue = (fieldName: string, value: string):void => {
  localStorage.setItem(fieldName, value)
}

const getStorageValue = (fieldName: string):string | null => {
  return localStorage.getItem(fieldName)
}

const removeStorageValue = (fieldName: string):void => {
  localStorage.removeItem(fieldName)
}


export {
  setStorageValue,
  getStorageValue,
  removeStorageValue,
}